import {observable, action, runInAction, useStrict} from 'mobx'
import api from '../../api/index'


useStrict(true)

class SortStore {
    @observable sort = [];

    // constructor() {
    //     this.menu = observable([]);
    // }

    @action async getSort(parms) {
       let sorts = await api.getSorts()
       console.log(sorts.data.data.data)
        runInAction(() => {
            this.sort = sorts.data
        });
    };
}

var sortStore = new SortStore();
export default sortStore;


